import React from 'react';
import {
    activeDisLikeColorCB,
    activeLikeColorCB,
    addPost,
    delPost, saveNewPostTextCB,
    setNewPostText,
} from "../../../redux/profile_page_reducer";
import {MyPosts} from "./MyPosts";
import {connect} from "react-redux";
import {RootStateType} from "../../../redux/redux-store";

export type PostsData = {
    id: string
    message: string
    likesCount: number
    disLikesCount: number
    views: number
    isLike: boolean
    isDislike: boolean
    comments: number
    activeLikeColor: 'primary' | 'secondary'
    activeDisLikeColor: 'primary' | 'secondary'
}
export type ProfilePageType = {
    postsData: Array<PostsData>
    newPostText: string
}

const mapStateToProps = (state: RootStateType): ProfilePageType => {
    return {
        postsData: state.profilePage.postsData,
        newPostText: state.profilePage.newPostText
    }
}

export const MyPostsContainer = connect(mapStateToProps, {
    addPost,
    delPost,
    saveNewPostTextCB,
    setNewPostText,
    activeLikeColorCB,
    activeDisLikeColorCB
})(MyPosts);


